.start {
  opacity: 0;
}
.end {
  opacity: 1;
  transition: opacity 0.5s;
}

.last-items{
  width: fit-content;
  height : 80%;
  position: relative;
  background-color: #ddd;
  margin-top: 10px;
  align-items: center;
  vertical-align: auto;
  border-style: dashed;

}

.main-bg {
  height: 300px;
  background-image: url('./bg.png');
  background-size: cover;
  background-position: center;

}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}